.navbar {
  width: 10vw;
  height: 100vh;
  display: flex;
}

.main-menu {
  width: 15%;
  position: absolute;
  top: 0;
  left: 5%;
  padding-right: 10px;
  margin-top: 46px;
  -webkit-transition: all 0.6s ease-in-out;
  -moz-transition: all 0.6s ease-in-out;
  transition: all 0.6s ease-in-out;
  display: none;
}
@media (min-width: 992px) {
  .main-menu {
    display: block;
  }
}
@media (min-width: 1400px) {
  .main-menu {
    margin-top: 186px;
  }
}
.main-menu-pgactive {
  left: 0%;
}
.main-menu-list {
  margin: 0;
  padding: 0;
}
.main-menu-list li {
  padding: 0;
  margin: 0;
  margin-bottom: 5px;
  list-style: none;
}
.main-menu-list a {
  display: block;
  background: #08aeac;
  padding: 10px;
  color: #ffffff;
  text-transform: uppercase;
  font-size: 12px;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  position: relative;
  overflow: hidden;
  z-index: 1;
}
.main-menu-list a:active,
.main-menu-list a:visited {
  text-decoration: none;
}
.main-menu-list a:before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 0;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  background: #021533;
  z-index: -1;
}
.main-menu-list a:hover {
  color: #ffffff;
  border: 0px solid transparent !important;
  text-decoration: none;
  padding-left: 15px;
}
.main-menu-list a:hover:before {
  width: 100%;
}
.main-menu-list a.menuActive {
  text-decoration: none;
  background: #021533;
  padding-left: 15px;
}
